<template>
  <div class="home-container">
    <van-nav-bar title="主页内容" fixed />
    <van-pull-refresh v-model="refreshing" @refresh="onRefresh" :disabled="finished">
      <van-list v-model="loading" :finished="finished" finished-text="没有更多了" @load="onLoad">
        <ArticleInfo v-for="item in articleList" :key="item.id" :title="item.title" :author="item.aut_name" :comment="item.comm_count" :date="item.pubdate" :cover="item.cover"></ArticleInfo>
      </van-list>
    </van-pull-refresh>
  </div>
</template>

<script>
import { getArticleListAPI } from '@/api/articleAPI.js'
import ArticleInfo from '@/components/ArticleInfo/ArticleInfo.vue'
export default {
  components: {
    ArticleInfo
  },
  data() {
    return {
      page: 1,
      limit: 10,
      articleList: [],
      loading: true,
      finished: false,
      refreshing: false
    }
  },
  name: 'Home',
  created() {
    this.initArticleList(false)
  },
  methods: {
    async initArticleList(Refresh) {
      const { data: res } = await getArticleListAPI(this.page, this.limit)
      console.log(res)
      if (Refresh) {
        this.articleList = [...res, ...this.articleList]
        this.refreshing = false
      } else {
        this.articleList = [...this.articleList, ...res]
        this.loading = false
      }

      if (res.length === 0) {
        this.finished = true
      }
    },
    onLoad() {
      console.log('出发了onLoad方法')

      this.page++
      this.initArticleList(false)
    },
    onRefresh() {
      this.page++

      // 重新加载数据
      // 将 loading 设置为 true，表示处于加载状态
      this.initArticleList(true)
    }
  }
}
</script>

<style lang="less" scoped>
.home-container {
  padding: 46px 0 50px 0;
  .van-nav-bar {
    background-color: #007bff;
    /deep/ .van-nav-bar__title {
      color: white;
    }
  }
}
</style>